html {
    height: 100%;
}

body {
    min-height: 100%;
    position: relative;
    margin:0;
    padding:0;
    box-sizing: border-box;
}

#root{
    overflow-x: hidden;
}
body {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #ffebaf;
}

.content-margin{
    margin-top: 3.1875rem;
    margin-bottom: 3.1875rem;
}
.nav {
    width: 100%;
    top: 0;
    background-color: #ffa094;
    padding: 0.5rem 0 0.5rem 0;
    box-shadow:0 1px 6px rgba(2, 15, 7, 0.502);
    position: fixed;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: #333745;
    z-index: 40;
}

.logo {
    width: 50%;
    margin-left: 0.5rem;
    z-index: 25;
}

.logoImg {
    width: 2.5rem;
    z-index: 26;
    margin: 0 0.25rem;
}
/* 
img {
    width: 3rem;
} */

.nav-right {
    width: 50%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    margin-right: 0.5rem;
}

.nav-right-login {
    width: 50%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
}

.nav-right-login-links{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
}

.nav-right-links{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
}

.nav-right-login div {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
}

.nav-toggle-icon-login{
    display:none;
    font-size: 2rem;
    cursor: pointer;
}

.nav-right div {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
}

.nav-category {
    display: none;
}

.nav-right-dropdown{
    display: none;
    background-color: #ffa094;
    margin-top: 4.5rem;
    right: 0;
    position: fixed;
    box-shadow:0 1px 6px rgba(2, 15, 7, 0.502);
}

.nav-right-dropdown-logged-in{
    display: none;
    background-color: #ffa094;
    margin-top: 4.5rem;
    right: 0;
    position: fixed;
    box-shadow:0 1px 6px rgba(2, 15, 7, 0.502);
}

.nav-right-dropdown-link {
    padding-top: 0.5rem;
    padding-bottom: 0.75rem;
    cursor: pointer;
}

.logo a{
    text-decoration: none;
    color: #333745;
}

.login-link a{
    text-decoration: none;
    color: #333745;
}

.register-link a {
    text-decoration: none;
    color: #333745;
}

.category-link a {
    text-decoration: none;
    color: #333745;
}

.nav-right-dropdown-link a {
    text-decoration: none;
    color: #333745;
}

@media screen and (max-width: 34rem){
    .nav-toggle-icon-login{
        display: block;
        cursor: pointer;
    }

    .nav-right-login-links {
        display: none;
    }

    .nav-right-links{
        display: none;
    }

    .nav-category {
        display: block;
    }

    .nav-right-dropdown-logged-in{
        margin-top: 9.5rem;
    }
}


.footer {
    background-color : #333745;
    font-size: 0.75rem;
    width: 100%;
    right: 0;
    bottom: 0;
    left: 0;
    margin: 0 auto;
    padding: 1rem 0 1rem 0;
    color: white;
    text-align: center;
    position: absolute;
}
/* ========================================= CAROUSEL */

.carouselWrapper{
    position:relative;
}

.carouselText{
    text-align: center;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    position: absolute;
    /* top: 3rem; */
    width: 100%;
    z-index: 20;
}

.carouselText h1{
    font-family: "Bad Script", cursive;
    font-size: 3rem;
    font-weight: 1200;
    color: white;
    text-shadow:3px 4px 5px #ffa094;
    position: absolute;
    margin-top: 2rem;
    z-index: 3;
}

.mainLogo {
    width: 20rem;
}


.splash-header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align:center;
    width: 100%;
    height: 100%;
    margin-top:3.2rem;
}

.splashHeaderImage{
    width: 100vw;
}

.splash-image {
    width: 100%;
    height: 100%;
    object-fit : cover;
}

/* ========================================= SPLASH*/

.splashTextContent{
    margin:3rem auto 0;
    width:80%;
}

.splashTextContent h1{
    text-align: center;
    font-family: 'Bad Script', cursive;
    font-size: 	3.125rem;
    font-weight: 700;
    color: #87cab0;
    margin-bottom:.15rem;
    padding-bottom:.15rem;
}

@media screen and (max-width: 500px) {
    .splashTextContent h1 {
        font-size: 2rem;
    }
}

.questionMark{
    letter-spacing:.4rem;
}

