.food, .active, .culture, .events{
    display: flex;
    justify-content:center;
    align-content:center;
    flex-direction:column;
    height:115px;
    width:115px;
    margin: 0rem 0.5rem 1rem;
    border-radius:100%;
    background-color: #87cab0;
    border-color: white;
    border-width:5px;
    border-style:solid;
    text-align:center;
    vertical-align: middle;
    font-family:'Bad Script',cursive;
    text-decoration:none;
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
    font-size:3rem;
    font-weight: 800;
    -webkit-transition: -webkit-transform 2s;
    transition: -webkit-transform 2s;
    transition: transform 2s;
    transition: transform 2s, -webkit-transform 2s;
    -webkit-transform: scale(1);
            transform: scale(1) 


}

.food p, .active p, .culture p, .events p{
    opacity: 0;
    color: #333745;
    -webkit-transition: all 2s;
    transition: all 2s;
    font-size:3rem;
    font-weight: 800;
    -webkit-transition-property:opacity;
    transition-property:opacity;
    -webkit-transition: opacity(0) 2s;
    transition: opacity(0) 2s;
    text-decoration:none;
   
}

.food{
    background-image: url('/images/icons/foodIcon.png');
    background-size:60%;
    background-repeat: no-repeat;
    background-position: center;
}

.active{
    background-image: url('/images/icons/activeIcon.png');
    background-size:60%;
    background-repeat: no-repeat;
    background-position: center;
}

.culture{
    background-image: url('/images/icons/cultureIcon.png');
    background-size:60%;
    background-repeat: no-repeat;
    background-position: center;
}

.events{
    background-image: url('/images/icons/eventsIcon.png');
    background-size:60%;
    background-repeat: no-repeat;
    background-position: center;
}

.food:hover, .active:hover, .culture:hover, .events:hover {
    -webkit-transform:scale(.75);
            transform:scale(.75);
    -webkit-transition: 2s all;
    transition: 2s all;
    background-color: rgb(135,202,176,0.75);
   
}

.food:hover p, .active:hover p, .culture:hover p, .events:hover p{
   opacity: 1;
    -webkit-transition: 2s all;
    transition: 2s all;
    
}

