.nav {
    width: 100%;
    top: 0;
    background-color: #ffa094;
    padding: 0.5rem 0 0.5rem 0;
    box-shadow:0 1px 6px rgba(2, 15, 7, 0.502);
    position: fixed;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: #333745;
    z-index: 40;
}

.logo {
    width: 50%;
    margin-left: 0.5rem;
    z-index: 25;
}

.logoImg {
    width: 2.5rem;
    z-index: 26;
    margin: 0 0.25rem;
}
/* 
img {
    width: 3rem;
} */

.nav-right {
    width: 50%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    margin-right: 0.5rem;
}

.nav-right-login {
    width: 50%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
}

.nav-right-login-links{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
}

.nav-right-links{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
}

.nav-right-login div {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
}

.nav-toggle-icon-login{
    display:none;
    font-size: 2rem;
    cursor: pointer;
}

.nav-right div {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
}

.nav-category {
    display: none;
}

.nav-right-dropdown{
    display: none;
    background-color: #ffa094;
    margin-top: 4.5rem;
    right: 0;
    position: fixed;
    box-shadow:0 1px 6px rgba(2, 15, 7, 0.502);
}

.nav-right-dropdown-logged-in{
    display: none;
    background-color: #ffa094;
    margin-top: 4.5rem;
    right: 0;
    position: fixed;
    box-shadow:0 1px 6px rgba(2, 15, 7, 0.502);
}

.nav-right-dropdown-link {
    padding-top: 0.5rem;
    padding-bottom: 0.75rem;
    cursor: pointer;
}

.logo a{
    text-decoration: none;
    color: #333745;
}

.login-link a{
    text-decoration: none;
    color: #333745;
}

.register-link a {
    text-decoration: none;
    color: #333745;
}

.category-link a {
    text-decoration: none;
    color: #333745;
}

.nav-right-dropdown-link a {
    text-decoration: none;
    color: #333745;
}

@media screen and (max-width: 34rem){
    .nav-toggle-icon-login{
        display: block;
        cursor: pointer;
    }

    .nav-right-login-links {
        display: none;
    }

    .nav-right-links{
        display: none;
    }

    .nav-category {
        display: block;
    }

    .nav-right-dropdown-logged-in{
        margin-top: 9.5rem;
    }
}

